/*GLOBAL STYLES*/
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body,
#root {
    height: 100vh;
}

body {
    overflow: auto;
    overflow-x:hidden;
}

#root {
    display: flex;
    flex-direction: column;
}

#root > main {
    flex-grow: 1;
    justify-content: center;
}
/*END GLOBAL STYLES*/

/*HEADER STYLES*/
#layout-header {
    padding-top: 1rem;
    padding-bottom: 0;
    /* margin-bottom: 70px; */
    /* flex-wrap: nowrap; */
    z-index: 3;
    .navbar-brand {
        padding-top: 0;
        padding-bottom: 0;
    }

    #cuadrado-logout {

        &.logout-responsive {
            margin : 0
        }

        @media screen and (min-width:990px){
            &.logout-responsive {
                display : none;
            }
        }

        @media screen and (max-width:990px){
            &.logout-desktop {
                display : none;
            }
        }
    }

}

#layout-header-logo {
    //margin-left: -18px;
}
/*END HEADER STYLES*/

/*MAIN STYLES*/
#layout-main {
    display: flex;
    flex-direction: column;
}

/*END MAIN STYLES*/

/*LANDING STYLES*/
#landing {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    position: relative;

    > svg {
        position: absolute;
        top: 0;
        right: 0;
        transition: all 0.3s ease;
        @media screen and (min-width : 1000px) {
            width: 60%;
        }
    }

    > div {
        width: 100%;
        z-index: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        svg {
            opacity: 0.3;
            transition: all 0.3s ease;
            @media screen and (min-width : 1000px) {
                max-width: 700px;
                opacity: 1;
            }
        }

        #landing-banner {
            position: absolute;
            top: 20%;
            display: flex;
            flex-direction: column;
            transition: all 0.3s ease;
            @media screen and (min-width : 1000px) {
                position: relative;
                top: initial;
            }
        }
    }
}
/*END LANDING STYLES*/










